
import { defineComponent } from 'vue'
import SearchBar from '@/components/SearchBar.vue'
import Pagination from '@/components/Pagination.vue'
import DrugList from '@/components/DrugList.vue'
import { apiBaseUrl } from '@/env'

interface CompData {
  drugs: any[]
  query: string
  lastPage: number
  showList: boolean,
  error: boolean
}

export default defineComponent({
  name: "Home",
  components: {
    SearchBar,
    Pagination,
    DrugList,
  },

  data: function (): CompData {
    return {
      drugs: [],
      query: '',
      lastPage: 1,
      showList: false,
      error: false
    }
  },

  methods: {
    onSearch(query: string) {
      this.resetUI()
      this.query = query
      this.makeFetchRequest()
    },

    onPageChanged(page: number) {
      this.makeFetchRequest(page)
    },

    makeFetchRequest(page = 1) {
      const url = new URL(`${apiBaseUrl}/search`)
      url.searchParams.append('query', this.query)
      url.searchParams.append('page', page.toString())
      fetch(url.toString(), { method: 'get' })
        .then((response) => {
          if (!response.ok)
            throw new Error('Unable to get results from server.')
          return response.json()
        })
        .then((data) => {
          this.showList = true
          this.lastPage = data.last_page
          this.drugs = data.drugs
        })
        .catch((error) => {
          this.error = true
          console.log(error)
        })
    },

    resetUI() {
      this.showList = false
      this.error = false
      this.lastPage = 1
      this.drugs = []
    },
  }
})
