
import { defineComponent } from 'vue'

export default defineComponent({
  name: 'SearchBar',

  data: function() {
    return {
      term: ''
    }
  },

  activated: function() {
    const q = this.$route.query.q
    if (q && typeof q == 'string') {
      const trimmed = q.trim()
      if (trimmed !== this.term) {
        this.term = trimmed
        this.$emit('search', trimmed)
      }
    }
  },

  methods: {
    onSubmit() {
      const trimmed = this.term.trim()
      if (trimmed.length != 0)
        this.$emit('search', trimmed)
    }
  }
})
